<template>
  <div>
    <b-row class="mx-2">
      <b-col cols="12" md="5" class="set_l my-1">
        <h4><b-icon icon="box-arrow-in-down" variant="success" class="mr-1"></b-icon>นำเข้าข้อมูล</h4>
      </b-col>
    </b-row>
    <div v-if="isHidden">
      <b-row class="my-2">
        <b-col cols="12">
          <b-row>
            <b-col cols="12" md="3" class="set_r">
              <b-form-select v-model="select_prov" :options="option_prov"></b-form-select>
            </b-col>
            <b-col cols="12" md="3">
              <b-form-select v-model="select_table" :options="option_table"></b-form-select>
            </b-col>
            <b-col cols="12" md="6" class="set_l">
              <b-form-input id="input-live" type="number" v-model="prototype" :state="eidcheck"
                aria-describedby="input-live-help input-live-feedback" placeholder="ข้อมูล" trim></b-form-input>
              <b-form-invalid-feedback id="input-live-feedback">
                ต้องมากกว่า 2 ตัวเลข
              </b-form-invalid-feedback>
            </b-col>
          </b-row>

          <b-row class="my-2">
            <b-col cols="6" md="2">
              <b-form-spinbutton v-model="yy" min="2022" max="2030" v-on:change="set_date_save"
                wrap></b-form-spinbutton>
            </b-col>
            <b-col cols="6" md="2">
              <b-form-spinbutton v-model="mm" min="1" max="12" v-on:change="set_date_save" wrap></b-form-spinbutton>
            </b-col>
            <b-col cols="12" md="4" class="set_c p-2">
              วันที่&nbsp;:&nbsp;<strong>{{ date_save }}</strong>
            </b-col>
            <b-col cols="12" md="4">
              <b-form-text id="input-live-help"></b-form-text>
              <b-button block :disabled="prototype.length <= 1" @click="check_update_group"
                variant="success">บันทึก</b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <!---      body         -->
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isHidden: true,
      select_prov: "NMA",
      option_prov: [{
        label: 'กรุณาเลือก จังหวัด',
        options: [
          { value: "NMA", text: "NMA" },
          { value: "CPM", text: "CPM" },
          { value: "BRM", text: "BRM" },
          { value: "SRN", text: "SRN" },
          { value: "UBN", text: "UBN" },
          { value: "SSK", text: "SSK" },
          { value: "YST", text: "YST" },
          { value: "ACR", text: "ACR" },
        ]
      }],
      select_table: "sumary_revennu",
      option_table: [{
        label: 'กรุณาเลือก',
        options: [
          { value: "sumary_revennu", text: "ข้อมูลสรุปจัดเก็บรายได้" },
          { value: "target_revennu", text: "เป้าหมายจัดเก็บรายได้" },
        ]
      }],
      prototype: "",
      date_save: '',
      mm: 1,
      yy: 2023,
    };
  },
  computed: {
    eidcheck() {
      var self = this;
      return self.prototype.length > 1 ? true : false;
    },
  },

  methods: {
    check_update_group() {
      var self = this;
      if (self.select_table == null) {
        self.makeToast("แจ้งเตือน!", "ยังไม่เลือกประเภท!", "warning");
      } else if (self.select_prov == null) {
        self.makeToast("แจ้งเตือน!", "ยังไม่เลือกจังหวัด!", "warning");
      } else {
        self.insert_data();
      }
    },
    insert_data() {
      var self = this;
      self.makeToast2("ผลลัพธ์!", "เพิ่มข้อมูลเรียบร้อย", "success");
    },
    makeToast(head, body, color) {
      var self = this;
      self.$bvToast.toast(body, {
        title: head,
        variant: color,
        autoHideDelay: 5000,
        solid: true,
        toaster: "b-toaster-bottom-center",
      });
    },
    makeToast2(head, body, color) {
      var self = this;
      self.$bvToast.toast(body, {
        title: head,
        variant: color,
        autoHideDelay: 5000,
        solid: true,
        toaster: "b-toaster-bottom-right",
      });
    },
    set_date_save() {
      var self = this;
      var month = "";
      if (self.mm < 10) {
        month = "0" + parseInt(self.mm);
      } else {
        month = self.mm;
      }
      self.date_save = self.yy + "-" + month + "-01";
    }
  },
  mounted: function () {
    var self = this;
    var date = new Date();
    self.yy = date.getFullYear();
    self.set_date_save();
  },
};
</script>
