        const target = Math.floor(Math.random() * fluctuation + baseMonthlyTarget);
        targetData.push(target);
        currentAccumRevenue += revenue;
        currentAccumTarget += target;
        accumRevenueData.push(currentAccumRevenue);
        accumTargetData.push(currentAccumTarget);
      }
