    generateRandomArray2(count) {
      if (typeof count !== 'number' || count <= 0) {
        console.error("Please provide a positive number.");
        return [];
      }
      const randomArray = [];
      const min = 1;
      const max = 1000000;
      let currentMin = min;
      for (let i = 0; i < count; i++) {
        const randomNumber = Math.floor(Math.random() * ((max + currentMin) - currentMin + 1)) + currentMin;
        randomArray.push(randomNumber);
        currentMin = randomNumber;
      }
      return randomArray;
    },
    remove_chart(value) {
      document.getElementById(value).remove();
      $("#S" + value).append("<canvas id=" + value + " ><canvas>");
    },
    toggle_open(value) {
      $("#S" + value).show();
    },
    toggle_close(value) {
      $("#S" + value).hide();
    },
    open_modal_load() {
      var self = this;
      self.$bvModal.show("modal_loading");
    },
    close_modal_load() {
      var self = this;
      setTimeout(() => {
        self.$bvModal.hide("modal_loading");
      }, 1000);
    },
    change_million(number) {
      var array = [];
      if (
        typeof number != "undefined" &&
        number != null &&
        number.length != null &&
        number.length > 0
      ) {
        number.forEach((element) => {
          var value = element / 1000000;
          if (value != 0) {
            array.push(value.toFixed(2));
          } else if (element == 0) {
            array.push(0);
          } else {
            array.push(null);
          }
        });
      }
      return array;
    },
