import Vue from "vue";
import Router from "vue-router";
import home from "../views/home/home.vue";
import chart_chanel from "../views/chart/chart_chanel.vue";
import m_package from "../views/manage/package.vue";
import insert_db from "../views/import/insert_db.vue";
import check_db from "../views/import/check_db.vue";

Vue.use(Router);

const routes = [
  {
    path: "/",
    name: "home",
    component: home,
    meta: {
      title: "เมนูหลัก",
    },
  },
  {
    path: "/chart-chanel",
    name: "chart_chanel",
    component: chart_chanel,
    meta: {
      title: "กราฟจัดเก็บรายได้",
    },
  },
  {
    path: "/manage-package",
    name: "m_package",
    component: m_package,
    meta: {
      title: "จัดการแพ็คเกจ",
    },
  },
  {
    path: "/insert-database",
    name: "insert_db",
    component: insert_db,
    meta: {
      title: "นำเข้าข้อมูล",
    },
  },
  {
    path: "/check-database",
    name: "check_db",
    component: check_db,
    meta: {
      title: "ตรวจสอบข้อมูล",
    },
  },
];
const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  next();
});

export default router;
