      self.lineSSK = response.SSK;
      self.lineYST = response.YST;
      self.lineACR = response.ACR;
      self.lineLabel = response.LABEL;
      self.lineACCUM = response.ACCUM;
      self.load_chart1();
    },
    load_chart1() {
      var self = this;
      var ctx = document.getElementById("myChart1");
      var chartData = {
