            //     position: "right",
            //     reverse: true,
            //     id: "y-axis-2",
            //     gridLines: {
            //       drawOnChartArea: false,
            //     },
            //   }
            // ],
          },
          legend: {
            position: "right",
          },
        },
      });

    },
    get_chart2() {
      var self = this;
      var response2 = self.$parent.sale_target;
      self.targetNMA = response2.NMA;
      self.targetCPM = response2.CPM;
      self.targetBRM = response2.BRM;
      self.targetSRN = response2.SRN;
      self.targetUBN = response2.UBN;
      self.targetSSK = response2.SSK;
      self.targetYST = response2.YST;
      self.targetACR = response2.ACR;
      self.targetLabel = response2.LABEL;
      self.targetACCUM = response2.ACCUM;
      self.load_chart2();
    },
    load_chart2() {
      var self = this;
      var ctx = document.getElementById("myChart2");
      var chartData = {
        labels: self.targetLabel,
        datasets: [
          // {
          //   type: "line",
          //   label: "เป้าสะสม",
          //   borderColor: self.$parent.getRandomColor(),
          //   fill: false,
          //   yAxisID: "y-axis-2",
          //   data: self.change_million(self.targetACCUM),
          // },
          {
            type: "line",
            label: "NMA",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetNMA),
            fill: false,
          },
          {
            type: "line",
            label: "CPM",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetCPM),
            fill: false,
          },
          {
            type: "line",
            label: "BRM",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetBRM),
            fill: false,
          },
          {
            type: "line",
            label: "SRN",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetSRN),
