          {
            type: "line",
            label: "YST",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetYST),
            fill: false,
          },
          {
            type: "line",
            label: "ACR",
            borderColor: self.$parent.getRandomColor(),
            data: self.change_million(self.targetACR),
            fill: false,
          },
        ],
      };

      var myBarChart = new Chart(ctx, {
        type: "bar",
        data: chartData,
        options: {
          responsive: true,
          title: {
            display: true,
            text: "เป้าจัดเก็บรายได้ " + self.year + " (หน่วยเป็นล้านบาท)",
          },
          tooltips: {
            mode: "index",
            intersect: true,
          },
          elements: {
            line: {
              tension: 0.000001,
            },
          },
          scales: {
            // yAxes: [
            //   {
            //     display: true,
            //   },
            //   {
            //     type: "linear",
            //     display: true,
            //     position: "right",
            //     reverse: true,
            //     id: "y-axis-2",
            //     gridLines: {
            //       drawOnChartArea: false,
            //     },
            //   },
            // ],
          },
          legend: {
            position: "right",
          },
        },
      });

    },
    remove_chart(value) {
      document.getElementById(value).remove();
      $("#S" + value).append("<canvas id=" + value + " style='position: relative; height:50vh; width:70vw'><canvas>");
    },
    open_overlay() {
      var self = this;
      self.show = true;
    },
    close_overlay() {
      var self = this;
      setTimeout(() => {
        self.show = false;
      }, 1000);
    },
    change_million(number) {
      var array = [];
      if (
        typeof number != "undefined" &&
        number != null &&
        number.length != null &&
        number.length > 0
      ) {
        number.forEach((element) => {
          var value = element / 1000000;
          if (value != 0) {
            array.push(value.toFixed(2));
          } else {
            array.push(null);
          }
        });
      }
      return array;
    },
  },
  mounted: function () {
    var self = this;
    self.set_date_now();
  },
};
</script>
