import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    access: "Admin",
    access_ins: "Admin",
    show_access: "",
    show_access2: "",
    show_eid_user: "",
    show_name_user: "",
    show_email_user: "",
    show_company_user: "",
    device: "",
    colum1: "",
    colum2: "",
  },
  mutations: {
    change_eid(state, value) {
      state.show_eid_user = value;
    },
    change_name(state, value) {
      state.show_name_user = value;
    },
    change_email(state, value) {
      state.show_email_user = value;
    },
    change_company(state, value) {
      state.show_company_user = value;
    },
    change_device(state, value) {
      state.device = value;
    },
    change_colum1(state, value) {
      state.colum1 = value;
    },
    change_colum2(state, value) {
      state.colum2 = value;
    },
    change_access(state, value) {
      state.show_access = value;
    },
    change_access2(state, value) {
      state.show_access2 = value;
    },
  },
  getters: {
    check_access: (state) => state.access,
    check_access2: (state) => state.access_ins,
    web_access: (state) => state.show_access,
    web_access2: (state) => state.show_access2,
    login_eid: (state) => state.show_eid_user,
    login_name: (state) => state.show_name_user,
    login_email: (state) => state.show_email_user,
    login_company: (state) => state.show_company_user,
    get_device: (state) => state.device,
    get_colum1: (state) => state.colum1,
    get_colum2: (state) => state.colum2,
  },
});
